export default class Player {
    boost = 0;
    invisible = 0;
    invincible = 0;
    stunned = 0;

    /**
     * @param {string} id
     * @param {[number, number]} position
     * @param {boolean} it
     */
    constructor(id, position, it) {
        this.id = id;
        this.position = position;
        this.it = it;
        this.speed = 5 * (1 + +it);
    }
}
