import _ from "lodash";

import Player from "./Player";

export default class World {
    /**
     * @type {Object<string, Player>}
     */
    players = {};

    /**
     * @type {Object<[number, number], Player>}
     */
    grid = {};

    /**
     * @param {number} size
     */
    constructor(size) {
        this.size = size;

        const axis = _.range(size);
    }

    /**
     * @param {string} id
     * @returns {Player}
     */
    add(id) {
        const { size, grid, players } = this;
        let p = [_.random(size), _.random(size)];
        while (grid[p])
            p = [_.random(size), _.random(size)];

        const player = grid[p] = players[id] = new Player(id, p, _.isEmpty(players));
        return player;
    }

    /**
     * @param {Player} player
     * @param {number} dx
     * @param {number} dy
     * @returns {Player[]}
     */
    update(player, dx, dy) {
        console.log(`${dx} ${dy}`);
        return [];
    }

    /**
     * @param {Player} player
     */
    remove(player) {
        delete this.grid[player.position];
        delete this.players[player.id];
    }
}
